<template>
    <div class="backgroundImg">
        <div class="setMain" v-if="Wsetpage=='main'">
            <h1 class="setMainTex"><b>[2024 2학기] 1학년<br>단어선택 리스트</b></h1>
            <p class="setText">세트</p>
            <div class="setListItem" v-for="file in files">
                <div class="setListWord">
                    <p class="setListWordText">단어</p>
                </div>
                <h3 class="setListText" @click="changePageWords(file.name)">{{ file.name }}</h3>
            </div>
        </div>
        <div class="setWords" v-if="Wsetpage=='words'">
            <ul>
                <li v-for="a in fileForWord">
                    <span>{{ a.word }}</span><br>
                    <span>{{ a.meaning }}</span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import fileList from '@/assets/words/files.json';
import firJSON from '@/assets/words/firstWords.json';
import Swal from 'sweetalert2';

const files = fileList;
const firstJSON = firJSON
let fileForWord = firstJSON;

export default{
    name: 'SetPage',
    components: {

    },
    data(){
        return{
            files,
            Wsetpage: 'main',
            firstJSON,
            fileForWord,
            pages: ['중1 비상 8단원', '중1 비상 9단원', '중1 비상 10단원'],
        };
    },
    methods:{
        changePageWords: function(fileName){
            console.log(fileName);
            if(this.Wsetpage == 'main'){
                this.Wsetpage = 'words';
            } else if(this.Wsetpage == 'words'){
                this.Wsetpage = 'main';
            }
            if(fileName == '중1 비상 8단원'){
                this.fileForWord = this.firstJSON;
            }
            if(this.Wsetpage == 'words'){
                Swal.fire({
                    icon: 'info',
                    title: '개발중인 페이지입니다.',
                    text: '메인 메뉴로 돌아가려면 새로고침하십시오.',
                    showCancelButton: false,
                    confirmButtonText: '예', 
                    confirmButtonColor: '#429f50',
                })
            }
        }
    },
}
</script>
<style>
.backgroundImg{
    display:inline-block;
    position:relative;
    width:100vw;
    height:100vh;
    background-image: linear-gradient(180.00deg,rgb(206,255,185) 1.591%,rgba(255,255,255,0.2) 104.231%);
}
.setMainTex{
    position:absolute;
    left:9.4vw;
    right:21vw;
    top:8vh;
    bottom:90vh;
    color:black;
    font-size:2.5vh;
    font-weight:400;
}
.setText{
    position:inline-block;
    margin-top:18vh;
    width:90vw;
    margin-left:5vw;
    margin-right:5vw;
    text-align:center;
    border:0px solid black;
    border-bottom:1px solid black;
}
.setListItem{
    width:90vw;
    margin-left:5vw;
}
.setListWord{
    display:inline-block;
    background-color:rgb(196,196,196);
    border:1px solid rgba(0, 0, 0, 0);
    border-radius:10px;
}
.setListWordText{
    display:inline-block;
    margin-left:1vw;
    margin-right:1vw;
    margin-top:0;
    margin-bottom:0;
    padding-top:0.5vh;
    padding-bottom:0.5vh;
    padding-right:1vw;
    padding-left:1vw;
}
.setListText{
    display:inline-block;
    margin-left:5vw;
}
</style>